import "./ImportExport.scss"
import Header from "../../components/Header/Header";
import {Button, Form} from "react-bootstrap";
import React, {useRef} from "react";
import {useNavigate} from "react-router-dom";
import axios from "axios";
import fileDownload from "js-file-download"

export function ImportExport() {

    const navigate = useNavigate();

    const fileInputRef = useRef<HTMLInputElement>(null);

    async function exportData() {

        await axios.get("/export", {
            baseURL: "http://localhost:8080",
            responseType: "arraybuffer"
        }).then((response) => {
            console.log(response.data)
            fileDownload(response.data, "export.json");
        }).catch((error) => {
            console.log(error)
        })

    }

    async function importData() {

        let formData = new FormData();

        if (fileInputRef.current !== null && fileInputRef.current.files !== null) {
            // console.log(fileInputRef.current.files[0])
            formData.append("file", fileInputRef.current.files[0])
        }

        await axios.post("/import", formData,{
            baseURL: "http://localhost:8080",
            headers: {
                "Content-Type": 'multipart/form-data'
            }
        }).then((response) => {
            // console.log(response.data)
            // fileDownload(response.data, "export.json");
        }).catch((error) => {
            console.log(error)
        })
    }

    return (
        <div id="import-export-wrapper">
            <Header title="/"/>

            <div id="content-wrapper">
                <div className="window">
                    <h1></h1>
                    <div>
                        <div>,       !</div>
                        <div>  :</div>
                        <ul>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                        </ul>
                        <div className="text-danger text-center"> ,   ?</div>
                    </div>
                    <div className="buttons">
                        <Button variant={"danger"} onClick={() => exportData()}></Button>
                        <Button onClick={() => navigate(-1)}></Button>
                    </div>
                </div>
                <div className="window">
                    <h1></h1>
                    <div>
                        <div>,      !</div>
                        <div>            .</div>
                        <div className="text-danger text-center">  ,   ,   
                            
                            ""
                        </div>
                    </div>
                    <Form>
                        <Form.Control type="file" ref={fileInputRef}/>

                        <div className="buttons">
                            <Button variant={"danger"} onClick={() => importData()}></Button>
                            <Button onClick={() => navigate(-1)}></Button>
                        </div>
                    </Form>
                </div>
            </div>
        </div>
    )
}