import "./MainPage.scss";
import Header from "../../components/Header/Header";
import {Button, Card, Col, Row} from "react-bootstrap";
import {Navigate, useNavigate} from "react-router-dom";
import {useAppDispatch, useAppSelector} from "../../hooks";
import {useCookies} from "react-cookie";
import {setUser} from "../../features/auth/authSlice";
import axios from "axios";

export function MainPage() {

    const navigate = useNavigate();

    const auth = useAppSelector((state) => state.auth)
    const dispatch = useAppDispatch()
    const [cookies] = useCookies(['auth']);

    if (!auth.authorized) {
        if (cookies["auth"] !== undefined) {
            dispatch(setUser({
                id: cookies["auth"].id,
                login: cookies["auth"].login,
                name: cookies["auth"].name,
                role: cookies["auth"].role
            }))
        } else {
            return <Navigate to="/sign_in"/>;
        }
    }

    let content = <div></div>;

    let title = "";

    switch (auth.role) {
        case "CLIENT": {
            title = "";
            content =
                <>
                    <Card className="text-center">
                        <Card.Body>
                            <Card.Title> </Card.Title>
                            <Card.Text>  .</Card.Text>
                            <Button variant="primary" onClick={() => {
                                navigate("/orders-list?status=%3A%3A")
                            }}></Button>
                        </Card.Body>
                    </Card>
                    <Card className="text-center">
                        <Card.Body>
                            <Card.Title> </Card.Title>
                            <Card.Text>  .</Card.Text>
                            <Button variant="primary" onClick={() => {
                                navigate("/new-order")
                            }}></Button>
                        </Card.Body>
                    </Card>
                    <Card className="text-center">
                        <Card.Body>
                            <Card.Title> </Card.Title>
                            <Card.Text>   .</Card.Text>
                            <Button variant="primary" onClick={() => {
                                navigate("/orders-list?status=")
                            }}></Button>
                        </Card.Body>
                    </Card>
                </>;
            break;
        }
        case "ADMIN": {
            title = "";
            content =
                <>
                    <Card style={{width: "300px"}}>
                        <Card.Img variant="top" src="/BranchBackgroundSmall.png"/>
                        <Card.Body>
                            <Card.Title> </Card.Title>
                            <Card.Text> ,   .</Card.Text>
                            <Button variant="primary" className="text-center" onClick={async () => {

                                await axios.get("/api/branch/get_by_admin_id", {
                                    baseURL: "http://localhost:8080",
                                    params: {
                                        admin_id: auth.id
                                    }
                                }).then(async (response) => {
                                    navigate("/branch-page/" + response.data.id)
                                }).catch((error) => {
                                    console.log(error)
                                })

                            }}></Button>
                        </Card.Body>
                    </Card>
                    <Card style={{width: "300px"}}>
                        <Card.Img variant="top" src="/OrderBackgroundSmall.png"/>
                        <Card.Body>
                            <Card.Title> </Card.Title>
                            <Card.Text>   ,   .</Card.Text>
                            <Button variant="primary" onClick={async () => {

                                await axios.get("/api/branch/get_by_admin_id", {
                                    baseURL: "http://localhost:8080",
                                    params: {
                                        admin_id: auth.id
                                    }
                                }).then(async (response) => {
                                    navigate("/orders-list/?branch=" + encodeURIComponent(response.data.address))
                                }).catch((error) => {
                                    console.log(error)
                                })

                            }}></Button>
                        </Card.Body>
                    </Card>
                </>;
            break;
        }
        case "DIRECTOR": {
            title = " ";
            content =
                <>
                    <Card style={{width: "300px"}}>
                        <Card.Img variant="top" src="/BranchBackgroundSmall.png"/>
                        <Card.Body>
                            <Card.Title> </Card.Title>
                            <Card.Text> ,   .</Card.Text>
                            <Button variant="primary" className="text-center" onClick={async () => {
                                await axios.get("/api/branch/get_by_director_id", {
                                    baseURL: "http://localhost:8080",
                                    params: {
                                        director_id: auth.id
                                    }
                                }).then(async (response) => {
                                    navigate("/branch-page/" + response.data.id)
                                }).catch((error) => {
                                    console.log(error)
                                })
                            }}></Button>
                        </Card.Body>
                    </Card>
                    <Card style={{width: "300px"}}>
                        <Card.Img variant="top" src="/WarehouseBackgroundSmall.png"/>
                        <Card.Body>
                            <Card.Title> </Card.Title>
                            <Card.Text> ,   .</Card.Text>
                            <Button variant="primary" onClick={async () => {

                                await axios.get("/api/warehouse/get_by_director_id", {
                                    baseURL: "http://localhost:8080",
                                    params: {
                                        director_id: auth.id
                                    }
                                }).then(async (response) => {
                                    navigate("/warehouse-page/" + response.data.id)
                                }).catch((error) => {
                                    console.log(error)
                                })

                            }}></Button>
                        </Card.Body>
                    </Card>
                </>;
            break;
        }
        case "SUPERUSER": {
            title = "";
            content = <>
                <div className="column">
                    <Card className="mb-3">
                        <Row className="g-0">
                            <Col className="col-md-4">
                                <Card.Img style={{width: "100%", height: "100%"}} className="img-fluid rounded-start"
                                          variant="left" src="/ClientBackgroundSmall.png"/>
                            </Col>
                            <Col className="">
                                <Card.Body>
                                    <Card.Title></Card.Title>
                                    <Card.Text>    .</Card.Text>
                                    <Button variant="primary" className="text-center" onClick={() => {
                                        navigate("/clients-list")
                                    }}></Button>
                                </Card.Body>
                            </Col>
                        </Row>
                    </Card>
                    <Card className="mb-3">
                        <Row className="g-0">
                            <Col className="col-md-4">
                                <Card.Img style={{width: "100%", height: "100%"}} className="img-fluid rounded-start"
                                          variant="left" src="/OrderBackgroundSmall.png"/>
                            </Col>
                            <Col className="">
                                <Card.Body>
                                    <Card.Title></Card.Title>
                                    <Card.Text>    .</Card.Text>
                                    <Button variant="primary" className="text-center" onClick={() => {
                                        navigate("/orders-list")
                                    }}></Button>
                                </Card.Body>
                            </Col>
                        </Row>
                    </Card>
                </div>
                <div className="column">
                    <Card className="mb-3">
                        <Row className="g-0">
                            <Col className="col-md-4">
                                <Card.Img style={{width: "100%", height: "100%"}} className="img-fluid rounded-start"
                                          variant="left" src="/EmployeeBackgroundSmall.png"/>
                            </Col>
                            <Col className="">
                                <Card.Body>
                                    <Card.Title></Card.Title>
                                    <Card.Text>    .</Card.Text>
                                    <Button variant="primary" className="text-center" onClick={() => {
                                        navigate("/employees-list")
                                    }}></Button>
                                </Card.Body>
                            </Col>
                        </Row>
                    </Card>
                    <Card className="mb-3">
                        <Row className="g-0">
                            <Col className="col-md-4">
                                <Card.Img style={{width: "100%", height: "100%"}} className="img-fluid rounded-start"
                                          variant="left" src="/BranchBackgroundSmall2.png"/>
                            </Col>
                            <Col className="">
                                <Card.Body>
                                    <Card.Title></Card.Title>
                                    <Card.Text>    .</Card.Text>
                                    <Button variant="primary" className="text-center" onClick={() => {
                                        navigate("/branches-list")
                                    }}></Button>
                                </Card.Body>
                            </Col>
                        </Row>
                    </Card>
                    <Card className="mb-3">
                        <Row className="g-0">
                            <Col className="col-md-4">
                                <Card.Img style={{width: "100%", height: "100%"}} className="img-fluid rounded-start"
                                          variant="left" src="/WarehouseBackgroundSmall2.png"/>
                            </Col>
                            <Col className="">
                                <Card.Body>
                                    <Card.Title></Card.Title>
                                    <Card.Text>    .</Card.Text>
                                    <Button variant="primary" className="text-center" onClick={() => {
                                        navigate("/warehouses-list")
                                    }}></Button>
                                </Card.Body>
                            </Col>
                        </Row>
                    </Card>
                </div>
                <div className="column">
                    <Card>
                        <Card.Img variant="left" src="/ImportExportCardImg.png"/>
                        <Card.Body>
                            <Card.Title>/ </Card.Title>
                            <Card.Text> / </Card.Text>
                            <Button variant="primary" onClick={() => navigate("/import-export")}></Button>
                        </Card.Body>
                    </Card>
                </div>

            </>;
            break;
        }
    }

    return (
        <div id="main-page-wrapper">
            <Header title={title}/>
            <div id="content-wrapper">
                {content}
            </div>
        </div>
    );
}